// bumper
import React, { useEffect, useState } from 'react';
import { NavLink, useHistory } from 'react-router-dom';
import Menu from 'antd/lib/menu';
import Dropdown from 'antd/lib/dropdown';
import AuditOutlined from '@ant-design/icons/AuditOutlined';
import WalletOutlined from '@ant-design/icons/WalletOutlined';
import MenuFoldOutlined from '@ant-design/icons/MenuFoldOutlined';
import { showInfo } from './Modal';
import Config from '../config';
import './Header.scss';
import LOGO from '../resources/logo.png'

import ctx, { mapData, unmapActions } from '../events';

const localStyle = {
  iconStyle: {
    fontSize: '15px',
    verticalAlign: 'middle'
  },
  iconBigStyle: {
    fontSize: '32px',
    verticalAlign: 'middle',
    color: '#fff'
  }
}

const connectWallet = () => {
  const { chainAccount } = ctx.data;
  if (chainAccount) {
    showInfo({
      content: `Your wallet address is already connected:\n${chainAccount}`
    });
    return;
  }
  ctx.event.emit('connectWallet');
}

const menu = () => {
  return (
    <Menu>
      <Menu.Item>
        <a target="_blank" rel="noreferrer" href={Config.whitepaperLink}>
          whitepaper
        </a>
      </Menu.Item>
      <Menu.Item onClick={connectWallet}>
        <span>
          wallet
        </span>
      </Menu.Item>
    </Menu>
  );
};

const ComponentView = () => {
  const history = useHistory();
  // const location = useLocation();
  const [account, setAccount] = useState('');

  useEffect(() => {
    const lifetimeObj = {};
    // 页面初始化，监听钱包连接状态
    mapData({
      chainAccount(chainAccount) {
        console.log(chainAccount)
        if (chainAccount) {
          setAccount(chainAccount);
          // 如果已连接钱包，进入买入页面
          history.replace('/markets');
        } else {
          history.replace('/');
        }
      }
    }, ctx, lifetimeObj);
    return () => {
      unmapActions(lifetimeObj);
    };
  }, [history]);

  return (
    <div className="nav-header">
      <div className="container">
        <div className="logo-wrapper">
          <div className="logo logo-pc">
            <NavLink to="/markets">
              <img src={LOGO} alt="logo" />
            </NavLink>
            {/* <a className="whitepaper button" href={Config.whitepaperLink} target="_blank" rel="noreferrer">
              <AuditOutlined style={localStyle.iconStyle} />
              <span className="label">WHITEPAPERS</span>
            </a> */}
          </div>
          {/* <NavLink className="logo-mobile" to="/list">
            <img src="https://cdn.daikeniu.com/static/bumper.png" style="width:120px" alt="logo" />
          </NavLink> */}
          {/*<p className="logo-pc">Decentralizen Hash Mining</p>*/}
        </div>
        <div className="navigator">
          <NavLink className="navtab" to="/markets" >Markets</NavLink>
          <a className="navtab" href={Config.scanUrl + 'address/' + account} target="_blank">History</a>
          <NavLink className="navtab" to="/farms" >Farms</NavLink>
          {/* <NavLink className="navtab" to="/xbumper" >xBumper</NavLink> */}
        </div>
        <div className="wallet-pc button" onClick={connectWallet}>
          <WalletOutlined style={localStyle.iconStyle} />
          <span className="label">
            {account ?  account.slice(0,6)+'...'+account.slice(-4,account.length):'Connect Wallet'}
          </span>
        </div>
        <Dropdown overlay={menu} className="wallet-mobile" overlayClassName="menu-dropdown">
          <MenuFoldOutlined style={localStyle.iconBigStyle} />
        </Dropdown>
      </div>
    </div>
  );
};

export default ComponentView;
